export default {
  name: 'dialog-box',
  data() {
    return {
      showDialog: true,
      type: 'text',
      title: '',
      text: '',
      ptext: '',
      showCancelBtn: true,
      confirmText: '确定',
      cancelText: '取消',
      placeholder: '',
      theme: '#F64C4C',
      confirm: null,
      list: []
    }
  },
  methods: {
    cancelClick() {
      this.showDialog = false
    },
    confirmClick() {
      if (this.type === 'select' && !this.text) {
        return
      }
      this.confirm(this.text)
      this.showDialog = false
    },
    toSelect(item) {
      for (var i in this.list) {
        this.$set(this.list[i], 'isSelect', false)
      }
      this.$set(item, 'isSelect', true)
      this.text = item
    }
  },
  mounted() {
    this.text = this.content
    if (this.type === 'textarea') {
      this.$nextTick(() => {
        document.getElementById('tex').focus()
      })
    }
  }
}



